@use "../util" as *;

html {
    font-size: 100%;
    box-sizing: border-box;
    width: 100%;
}

*, *::before, *::after {
    box-sizing: inherit;
    margin: 0;
    padding: 0;  
}

body {
    font-family: var(--font-open-sans);
    background: var(--LightGrayishBlue);
    min-width: 100%;
}