.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0.9375rem;
}
@media (min-width: 71.25em) {
  .header {
    margin-inline: 1.5625rem;
    margin-block: 1.25rem;
    justify-content: space-between;
  }
}
.header__logo {
  grid-area: logo;
}
.header__logo .logo {
  max-width: 100%;
  height: auto;
  vertical-align: middle;
}
.header__nav {
  margin-left: 0.625rem;
}
@media (min-width: 71.25em) {
  .header__nav {
    margin: 0;
  }
}
.header__nav ul {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: end;
  vertical-align: middle;
}
.header__nav ul li {
  padding-inline-start: 0.625rem;
}
.header__nav ul li :hover {
  color: var(--DesaturatedBlue);
}
@media (min-width: 71.25em) {
  .header__nav ul li {
    padding: 0.9375rem;
  }
}
.header__nav ul li a {
  font-size: 0.9375rem;
  color: var(--VeryDarkBlue);
}

.first .card {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  flex-wrap: nowrap;
}
@media (min-width: 71.25em) {
  .first .card {
    flex-direction: row-reverse;
    flex-wrap: nowrap;
    align-items: center;
    margin: 0.9375rem;
  }
}
@media (min-width: 71.25em) {
  .first .card__image {
    width: 50%;
    margin-right: 0.9375rem;
  }
}
.first .card__image img {
  max-width: 100%;
  height: auto;
}
.first .card__heading {
  text-align: center;
  padding: 0.625rem;
  margin-inline: 0.9375rem;
}
@media (min-width: 71.25em) {
  .first .card__heading {
    font-size: 1.25rem;
    text-align: left;
    width: 80%;
    padding: 1.25rem;
  }
}
.first .card__heading h1 {
  line-height: 1.5;
  font-size: 1.875rem;
}
.first .card__explain {
  padding-block: 0.625rem;
  color: var(--VeryDarkBlue);
  font-size: 1.125rem;
  font-weight: 400;
  text-align: center;
  line-height: 1.6;
  margin: 0 0.3125rem;
}
@media (min-width: 71.25em) {
  .first .card__explain {
    font-size: 1.125rem;
    text-align: left;
    width: 80%;
    padding: 1.25rem;
  }
}
.first .card__getstarted form {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  width: 95%;
  margin: auto;
}
@media (min-width: 71.25em) {
  .first .card__getstarted form {
    flex-wrap: nowrap;
    justify-content: start;
    margin: 0;
    padding: 1.25rem;
  }
}
.first .card__getstarted form input[type=email] {
  align-self: center;
  border: 0.125rem solid var(--DesaturatedBlue);
  border-radius: 0.25rem;
  padding: 0.9375rem;
  width: 100%;
  margin-block-start: 0.9375rem;
  font-size: 1.125rem;
}
@media (min-width: 71.25em) {
  .first .card__getstarted form input[type=email] {
    width: 50%;
    position: relative;
  }
}
.first .card__getstarted form p {
  margin-bottom: 0.9375rem;
  margin-top: 0.3125rem;
}
@media (min-width: 71.25em) {
  .first .card__getstarted form p {
    position: absolute;
    margin: 0.3125rem 0 0 2.1875rem;
    left: 0;
    top: 410px;
  }
}
.first .card__getstarted form input[type=button] {
  align-self: inherit;
  border: none;
  border-radius: 0.25rem;
  width: 25rem;
  padding: 0.9375rem;
  text-align: center;
  align-self: center;
  background: var(--BrightBlue);
  color: var(--LightGrayishBlue);
  font-size: 1.125rem;
  font-weight: 700;
  width: 100%;
  cursor: pointer;
}
@media (min-width: 71.25em) {
  .first .card__getstarted form input[type=button] {
    width: 25%;
    margin-left: 1.25rem;
    margin-top: 0.9375rem;
  }
}
.first .card__getstarted form input[type=button]:hover {
  opacity: 0.9;
}

.second {
  grid-area: div2;
}
.second .card {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  flex-wrap: wrap;
}
@media (min-width: 71.25em) {
  .second .card {
    justify-content: baseline;
    flex-direction: row-reverse;
    flex-wrap: nowrap;
    padding-inline: 1.25rem;
  }
}
.second .card__image {
  margin-bottom: 3.125rem;
  margin-top: 3.125rem;
}
@media (min-width: 71.25em) {
  .second .card__image {
    width: 35%;
    height: auto;
    margin: 0;
  }
}
.second .card__image img {
  max-width: 100%;
  height: auto;
}
.second .card__heading {
  padding-block: 0.9375rem;
  text-align: center;
  color: var(--VeryDarkBlue);
}
@media (min-width: 71.25em) {
  .second .card__heading {
    width: 80%;
    text-align: start;
    padding-left: 0.9375rem;
    font-size: 1.25rem;
  }
}
.second .card__explain {
  padding: 0.625rem;
  text-align: start;
  color: var(--DesaturatedBlue);
  font-size: 1.125rem;
}
@media (min-width: 71.25em) {
  .second .card__explain {
    text-align: left;
    width: 60%;
  }
}
.second .card__explain p {
  margin-bottom: 0.9375rem;
}
.second .card__work {
  display: flex;
  justify-content: center;
  flex-direction: row;
}
@media (min-width: 71.25em) {
  .second .card__work {
    justify-content: start;
    padding-left: 0.625rem;
  }
}
.second .card__work :hover {
  color: rgba(60, 159, 143, 0.8);
}
.second .card__work a {
  color: var(--ModerateCyan);
  border-bottom: 0.125rem solid var(--ModerateCyan);
  padding: 0.3125rem;
  font-size: 1.125rem;
}
.second .card__work a img {
  margin-left: 0.5rem;
  max-width: 100%;
  height: auto;
  vertical-align: middle;
}
.second .card__comment {
  box-shadow: 0 0 0.4375rem 0.1875rem #BCBCBC;
  padding: 0.625rem;
  border-radius: 0.3125rem;
  background: var(--LightGrayishBlue);
  width: 95%;
  margin: 1.875rem auto;
}
@media (min-width: 71.25em) {
  .second .card__comment {
    margin-right: 3.125rem;
    width: 50%;
    margin: 1.875rem 0;
  }
}
.second .card__comment .quote {
  max-width: 100%;
  height: auto;
  margin: 1.25rem 0rem 0.625rem 0.5rem;
}
.second .card__comment p {
  padding-left: 0.5rem;
  color: var(--VeryDarkBlue);
  font-size: 1.125rem;
}
.second .card__comment .author {
  display: flex;
  justify-content: start;
  align-items: center;
  padding: 0.9375rem 0.5rem;
}
.second .card__comment .author .box img {
  border-radius: 50%;
  max-width: 100%;
  height: auto;
}
.second .card__comment .author .box2 {
  padding-left: 0.375rem;
  text-align: start;
}
.second .card__comment .author .box2 p {
  margin-bottom: 0.125rem;
  font-size: 1rem;
  font-weight: 700;
}
.second .card__comment .author .box2 span {
  color: var(--DesaturatedBlue);
  font-size: 0.8125rem;
  font-weight: 400;
}

.third {
  grid-area: div3;
