}
@media (min-width: 71.25em) {
  .third .card {
    flex-direction: row;
    flex-wrap: nowrap;
  }
}
.third .card__acces {
  margin-bottom: 0.9375rem;
  font-size: 1.25rem;
  text-align: center;
}
@media (min-width: 71.25em) {
  .third .card__acces {
    text-align: left;
    padding: 0.9375rem;
  }
}
.third .card__explain {
  margin-bottom: 0.9375rem;
  font-size: 1.125rem;
  padding: 0.625rem;
  text-align: center;
}
@media (min-width: 71.25em) {
  .third .card__explain {
    text-align: left;
    padding: 0.9375rem;
    width: 70%;
  }
}
.third .card form {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  width: 100%;
}
@media (min-width: 71.25em) {
  .third .card form {
    justify-content: start;
    width: 70%;
