}
.third .card form input[type=email] {
  align-self: center;
  border: 0.125rem solid var(--DesaturatedBlue);
  border-radius: 0.25rem;
  padding: 0.9375rem;
  width: 100%;
  margin: 0.9375rem;
  font-size: 1.0625rem;
  text-decoration: none;
}
@media (min-width: 71.25em) {
  .third .card form input[type=email] {
    align-self: baseline;
    width: 90%;
  }
}
.third .card form p {
  margin-bottom: 0.9375rem;
  color: var(--LightGrayishBlue) !important;
  margin-left: 0.3125rem;
}
@media (min-width: 71.25em) {
  .third .card form p {
    margin-left: 1.25rem;
  }
}
.third .card form input[type=button] {
  border: none;
  border-radius: 0.25rem;
  width: 100%;
  padding: 0.9375rem;
  text-align: center;
  align-self: center;
  background: var(--BrightBlue);
  color: var(--LightGrayishBlue);
  font-size: 1.0625rem;
  font-weight: 700;
  cursor: pointer;
}
@media (min-width: 71.25em) {
  .third .card form input[type=button] {
    width: 12.5rem;
    align-self: baseline;
    font-size: 0.9375rem;
    margin-left: 1.0625rem;
  }
}
.third .card form input[type=button]:hover {
  opacity: 0.8;
}

